<template>
<el-row>
  <el-col :span="6"  class="border">
    <h4>文件名生成</h4>
    <el-form :model="form">
      <el-form-item label="文件名称">
        <el-input v-model="form.name" placeholder="请输入文件名" clearable></el-input>
      </el-form-item>
      <el-form-item label="controller">
        <el-input v-model="controller" class="controller">
          <el-button slot="append" icon="el-icon-document" data-clipboard-target=".controller"></el-button>
        </el-input>
      </el-form-item>
      <input type="text" v-model="view" class="hiddenview hide">
      <el-form-item label="view">
        <el-input v-model="view" placeholder="view" class="view">
          <el-button slot="append" icon="el-icon-document" data-clipboard-target=".hiddenview"></el-button>
        </el-input>
      </el-form-item>
      <input type="text" v-model="menuCode" class="hiddenmenuCode hide">
      <el-form-item label="menuCode">
        <el-input v-model="menuCode" placeholder="menuCode" >
          <el-button id="copyBtn" slot="append" icon="el-icon-document" data-clipboard-target=".hiddenmenuCode"></el-button>
        </el-input>
      </el-form-item>
    </el-form>
  </el-col>
  <el-col :span="6"  class="border">
    <h4>grid列生成 </h4>
    <el-form ref="gridBuildForm" :model="form2" class="pl10">
      <el-row>
        <el-col :span="24">
         <el-input-number v-model="form2.inputLength" :min="1" :max="100" label="字段数量" size="mini"></el-input-number>
        </el-col>
        <el-col :span="11">
          <el-form-item label="title">
            <el-input class="pd10" v-for="(item, index) in form2.inputLength" :key="index" :name="index" v-model="title"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11" :offset="1">
          <el-form-item label="filed">
            <el-input class="pd10" v-for="(item, index) in form2.inputLength" :key="index" :name="index"></el-input>
          </el-form-item>
        </el-col>
        <el-button size="mini" type="primary" @click="buildGridCode">生成grid代码</el-button>
      </el-row>
    </el-form>
  </el-col>
</el-row>
</template>
<script>
import _ from 'lodash'
import Clipboard from 'clipboard'
export default {
  data () {
    return {
      form: {
        name: ''
      },
      form2: {
        inputLength: 0
      }
    }
  },
  computed: {
    controller: function () {
      return `${_.camelCase(
        this.form.name
      )}.controller.${this.form.name.replace(/\s/g, '')}Controller`
    },
    view () {
      return `${_.camelCase(this.form.name)}.view.${this.form.name.replace(
        /\s/g,
        ''
      )}View`
    },
    menuCode () {
      let words = this.form.name.toUpperCase().split(' ')
      let finalwords = words.join('_')
      console.log(finalwords)
      return `MENU_${finalwords}`
    }
  },
  methods: {
    onSubmit: function () { },
    buildGridCode () {
      console.log(this.$refs.gridBuildForm)
    }
  },
  mounted () {
    // eslint-disable-next-line
    var clipboard = new Clipboard("#copyBtn")
    clipboard.on('success', function (e) {
      console.info('Action:', e.action)
      console.info('Text:', e.text)
      console.info('Trigger:', e.trigger)
      e.clearSelection()
    })
    clipboard.on('error', function (e) {
      console.error('Action:', e.action)
      console.error('Trigger:', e.trigger)
    })
  }
}
</script>

<style>
.pl10 {
  padding-left: 10px;
}
.border {
  border: 1px solid #ebebeb;
  border-radius: 3px;
  padding: 10px;
}
.pd10 {
  padding: 10px 0;
}

.hide{
  display: none;
}
</style>



